body  {
    background-color: rgb(236, 241, 241);
}
.morty{
    position:absolute;
    top:30px;
   
    
  }
  .hair{
    position:absolute;
    left:590px;
    top:30px;
    width:270px;
    height:160px;
    background:#8A4D18;
    border-radius:50% 50% 20% 20%;
    border: 3px solid ;
  }
  .face{
    position:absolute;
    left:600px;
    top:70px;
    width:250px;
    height:250px;
    background:#FFD2A1;
    border-radius:47%;
    border: 3px solid ;
  }
  .ears{
    
    position:absolute;
    left:576px;
    top:170px;
    background:#FFD2A1;
    height:35px;
    width:30px;
    border-radius:50% 10% 10% 50%;
    border:2px solid;
    
  }
  .ears::after{
    content:"";
    position:absolute;
    left:267px;
    top:0px;
    background:#FFD2A1;
    height:35px;
    width:30px;
    border-radius:10% 50% 50% 10%;
    border:2px solid;
  }
  .eyes{
    position:absolute;
    top:130px;
    left:630px;
    width:70px;
    height:70px;
    background:white;
    border-radius:50%;
    border: 2px solid ;
    
  }
  .eyes::after{
    content:"";
    position:absolute;
    top:0px;
    left:120px;
    width:70px;
    height:70px;
    background:white;
    border-radius:50%;
    border: 2px solid ;
  }
  .eyes::before{
    content:"";
    position:absolute;
    top:33px;
    left:30px;
    width:10px;
    height:10px;
    background:black;
    border-radius:50%;
    z-index:50;
    -webkit-box-shadow:120px 0px;
            box-shadow:120px 0px;
    -webkit-animation: anim2 2s infinite;
            animation: anim2 2s infinite;
    
  }
  .mouth{
    position:absolute;
    top:250px;
    left:705px;
    height:20px;
    width:12px;
    border:2px solid black;
    border-top: 0px;
    border-radius: 0px 0px 45px 45px;
    -webkit-transform:rotatez(10deg);
        -ms-transform:rotate(10deg);
            transform:rotatez(10deg);
  }
  .mouth::after{
    content:"";
    position:absolute;
    top:7px;
    left:11.5px;
    height:20px;
    width:12px;
    border:2px solid black;
    border-top: 0px;
    border-radius: 0px 0px 45px 45px;
    -webkit-animation: anim .5s infinite;
            animation: anim .5s infinite;
    -webkit-transform-origin: -5% -10%;
        -ms-transform-origin: -5% -10%;
            transform-origin: -5% -10%;
    
  }
  .nose{
    position:absolute;
    top:200px;
    left:715px;
    height:20px;
    width:15px;
    border:2px solid black;
    border-top: 0px;
    border-radius: 0px 0px 45px 45px;
    -webkit-transform:rotatez(0deg);
        -ms-transform:rotate(0deg);
            transform:rotatez(0deg);
  }
  .lines{
    position:absolute;
    top:116px;
    left:643px;
    height:2px;
  width:50px;
  border:3.2px solid black;
  border-top: 0px;
    border-left: 0px;
    border-right: 0px;
  border-radius: 0px 0px 50% 50%;
    -webkit-transform:rotatez(170deg);
        -ms-transform:rotate(170deg);
            transform:rotatez(170deg);
  }
  .lines::before{
    content:"";
    position:absolute;
    top:-22px;
    right:120px;
    height:2px;
    width:50px;
    border:3.2px solid black;
    border-top: 0px;
    border-left: 0px;
    border-right: 0px;
    border-radius: 0px 0px 50% 50%;
    -webkit-transform:rotatez(20deg);
        -ms-transform:rotate(20deg);
            transform:rotatez(20deg);
  }
  h2{
    color:rgb(41, 39, 39);
    margin:0 auto
 }
  
  @-webkit-keyframes anim{
    0%{
      -webkit-transform:rotatez(0deg);
              transform:rotatez(0deg);
    }
    50%{
      -webkit-transform:rotatez(-10deg);
              transform:rotatez(-10deg)
    }
    100%{
      -webkit-transform:rotatez(0deg);
              transform:rotatez(0deg)
    }
  }
  
  @keyframes anim{
    0%{
      -webkit-transform:rotatez(0deg);
              transform:rotatez(0deg);
    }
    50%{
      -webkit-transform:rotatez(-10deg);
              transform:rotatez(-10deg)
    }
    100%{
      -webkit-transform:rotatez(0deg);
              transform:rotatez(0deg)
    }
  }
  @-webkit-keyframes anim2{
    0%{
      -webkit-transform:translatex(-20px);
              transform:translatex(-20px);
    }
    50%{
      -webkit-transform:translatex(20px);
              transform:translatex(20px);
    }
    100%{
      -webkit-transform:translatex(-20px);
              transform:translatex(-20px);
    }
  }
  @keyframes anim2{
    0%{
      -webkit-transform:translatex(-20px);
              transform:translatex(-20px);
    }
    50%{
      -webkit-transform:translatex(20px);
              transform:translatex(20px);
    }
    100%{
      -webkit-transform:translatex(-20px);
              transform:translatex(-20px);
    }
  }
  h3{
    position:absolute;
    top:-50px;
    left:560px;
    font-size:50px;
    font-family: 'Roboto', sans-serif;
    color:white;
    width:400px;
  }
  


